import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';

type HeightTypes = 'FULL_HEIGHT' | 'TWO_THIRDS';

type HeroImageProps = {
  image: IGatsbyImageData;
  size?: HeightTypes;
};

const HeroImage: React.FC<HeroImageProps> = ({
  image,
  size = `FULL_HEIGHT`,
}) => {
  const height: { [key in HeightTypes]: number } = {
    FULL_HEIGHT: 100,
    TWO_THIRDS: 66,
  };
  const { Image } = {
    Image: styled(GatsbyImage)`
      width: 100vw;
      height: ${height[size]}vh;
      background-color: white;
    `,
  };

  return <Image image={image} alt={`hero-image`} />;
};

type HeroImageGQL = {
  image: { gatsbyImageData: IGatsbyImageData };
  size: HeightTypes;
} & ComponentId;

export const query = graphql`
  fragment HeroImage on ContentfulHeroImage {
    image {
      gatsbyImageData
    }
    size
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getHeroImage: GetComponent = (componentId, component) =>
  isDataType<HeroImageGQL>(componentId, component, `heroImage`) ? (
    <HeroImage size={component.size} image={component.image.gatsbyImageData} />
  ) : undefined;

export default HeroImage;
