import { BLOCKS, MARKS } from '@contentful/rich-text-types';
import {
  ContentfulRichTextGatsbyReference,
  renderRichText,
  RenderRichTextData,
} from 'gatsby-source-contentful/rich-text';
import { ReactNode } from 'react';

export const renderStyledRichText = (
  data: RenderRichTextData<ContentfulRichTextGatsbyReference>,
  args?: { [key: string]: unknown },
) => {
  if (!data?.raw) {
    return <></>;
  }
  const options = {
    renderMark: {
      [MARKS.BOLD]: (text: ReactNode) => <b>{text}</b>,
      [MARKS.ITALIC]: (text: ReactNode) => <i>{text}</i>,
    },
    renderNode: {
      [BLOCKS.HEADING_1]: (_: any, children: ReactNode) => <h1>{children}</h1>,
      [BLOCKS.HEADING_2]: (_: any, children: ReactNode) => <h2>{children}</h2>,
      [BLOCKS.HEADING_3]: (_: any, children: ReactNode) => <h3>{children}</h3>,
      [BLOCKS.HEADING_4]: (_: any, children: ReactNode) => <h4>{children}</h4>,
      [BLOCKS.HEADING_6]: (_: any, children: ReactNode) => (
        <h6 className={args?.textSize ? (args.textSize as string) : undefined}>
          {children}
        </h6>
      ),
      [BLOCKS.PARAGRAPH]: (_: any, children: ReactNode) => (
        <p className={args?.textSize ? (args.textSize as string) : undefined}>
          {children}
        </p>
      ),
    },
  };
  return renderRichText(data, options);
};

export type Breakpoint = 768 | 1024;
