import { DefaultContainer } from '@/components';
import {
  Max1023View,
  Max767View,
  MaxWidth,
  Min1024View,
  Min768View,
} from '@/components/View';
import { Breakpoint } from '@/theme/utils';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import React, { ReactNode } from 'react';
import { renderGQLComponents } from '..';

type Props = {
  backgroundColor?: 'tinted' | 'white';
  breakPoint?: Breakpoint;
  maxWidth?: MaxWidth;
  style?: React.CSSProperties;
  defaultContainer?: boolean;
  children?: ReactNode;
};

const Row = styled.div`
  display: flex;
  flex-direction: row;
  flex: 0 1 auto;
  gap: 32px;
  > * {
    flex: 1;
  }
`;

const Horizontal: React.FC<Props> = ({
  backgroundColor = `white`,
  breakPoint = 768,
  maxWidth = `1392px`,
  style,
  defaultContainer,
  children,
}) => {
  const MaxContainer = breakPoint === 768 ? Max767View : Max1023View;
  const MinContainer = breakPoint === 768 ? Min768View : Min1024View;
  return (
    <DefaultContainer
      enable={defaultContainer}
      widthBoundaryProps={{
        backgroundStyle: style,
        maxWidth,
        backgroundColor,
      }}
    >
      <MinContainer>
        <Row id="row">{children}</Row>
      </MinContainer>
      <MaxContainer>{children}</MaxContainer>
    </DefaultContainer>
  );
};

export type HorizontalGQL = {
  items: any;
  backgroundColor?: 'tinted' | 'white';
  style?: Object;
} & ComponentId;

export const query = graphql`
  fragment Horizontal on ContentfulHorizontal {
    items {
      ...Vertical
      ...Image
      ...Text
    }
    style {
      marginTop
      marginBottom
      paddingTop
      paddingBottom
    }
    backgroundColor
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
  fragment HorizontalWOVertical on ContentfulHorizontal {
    items {
      ...Image
    }
    style {
      marginTop
      marginBottom
      paddingTop
      paddingBottom
    }
    backgroundColor
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getHorizontal: GetComponent = (componentId, component, args) =>
  isDataType<HorizontalGQL>(componentId, component, `horizontal`) ? (
    <Horizontal
      defaultContainer={args?.defaultContainer}
      style={component.style}
      backgroundColor={component.backgroundColor}
    >
      {renderGQLComponents(component.items, { defaultContainer: false })}
    </Horizontal>
  ) : undefined;

export default Horizontal;
