import { IGatsbyImageData } from 'gatsby-plugin-image';

export type ComponentIds =
  | 'squareImageWithText'
  | 'imageRow'
  | 'twoColumnImageWithText'
  | 'heroImage'
  | 'image'
  | 'text'
  | 'titles'
  | 'horizontal'
  | 'vertical'
  | 'byVtHeadline'
  | 'button'
  | 'imageList';

export type ListComponentIds = 'review';

export type ReferenceComponentId = `referenceList`;

export type ComponentId = {
  sys: { contentType: { sys: { id: string } } };
};

export type ReferenceListGQL = {
  title: string;
  list: (any & ComponentId)[];
};

export interface ProductPage {
  title: string;
  slug: string;
  hero?: {
    image: {
      gatsbyImageData: IGatsbyImageData;
    };
  };
  components: (any & ComponentId)[];
}

export function getComponentId(component: any & ComponentId) {
  return component?.sys?.contentType?.sys?.id;
}

export function isDataType<T>(
  inputComponentId: string,
  componentContent: any,
  componentId: ComponentIds | ReferenceComponentId,
): componentContent is T & ComponentId {
  return componentId === inputComponentId;
}

export function isListType<T>(
  componentContent: any[],
  componentId: ListComponentIds,
): componentContent is (T & ComponentId)[] {
  return componentContent.every((comp) => getComponentId(comp) === componentId);
}

export type GetComponent = (
  componentId: ComponentIds,
  component: any,
  args?: { [key: string]: any },
) => JSX.Element | undefined;
