import { colors } from '@/theme';
import { renderStyledRichText } from '@/theme/utils';
import styled from '@emotion/styled';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import {
  RenderRichTextData,
  ContentfulRichTextGatsbyReference,
} from 'gatsby-source-contentful/rich-text';
import React, { useMemo } from 'react';
import { Max767View, Min768View, WidthBoundary } from '@/components';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';

interface Props {
  images: [IGatsbyImageData, IGatsbyImageData];
  titleText: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  paragraphText: RenderRichTextData<ContentfulRichTextGatsbyReference>;
}

const {
  Columns,
  Container,
  ImagesContainer,
  Image,
  NarrowContainer,
  NarrowTitleContainer,
} = {
  ImagesContainer: styled.div`
    display: flex;
    overflow: hidden;
    flex-direction: row;
    justify-content: center;
  `,
  Image: styled(GatsbyImage)`
    :first-of-type {
      margin-right: 16px;
    }
    :last-of-type {
      margin-left: 16px;
    }
  `,
  Container: styled.div`
    margin: auto;
    width: 75%;
    margin-top: 48px;
    margin-bottom: 48px;
  `,
  Columns: styled.div`
    column-count: 2;
    column-gap: 32px;
  `,
  NarrowContainer: styled.div`
    padding: 32px;
  `,
  NarrowTitleContainer: styled.div`
    margin-top: 32px;
  `,
};

const TwoColumnImageWithText: React.FC<Props> = ({
  images,
  titleText,
  paragraphText,
}) => {
  const imagesComponents = useMemo(
    () =>
      images.map((image, index) => (
        <Image key={index} image={image} alt={`Afbeelding ${index} in rij`} />
      )),
    [images],
  );

  return (
    <WidthBoundary
      maxWidth="1392px"
      backgroundStyle={{ backgroundColor: colors.backgroundTint }}
    >
      <Min768View>
        <Container>
          <ImagesContainer>{imagesComponents}</ImagesContainer>
          {renderStyledRichText(titleText)}
          {paragraphText.raw && (
            <Columns>{renderStyledRichText(paragraphText)}</Columns>
          )}
        </Container>
      </Min768View>
      <Max767View>
        <NarrowContainer>
          {imagesComponents}
          <NarrowTitleContainer>
            {renderStyledRichText(titleText)}
          </NarrowTitleContainer>
          {renderStyledRichText(paragraphText)}
        </NarrowContainer>
      </Max767View>
    </WidthBoundary>
  );
};

type TwoColumnImageWithTextGQL = {
  images: [
    { gatsbyImageData: IGatsbyImageData },
    { gatsbyImageData: IGatsbyImageData },
  ];
  titleText: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  paragraphText: RenderRichTextData<ContentfulRichTextGatsbyReference>;
} & ComponentId;

export const query = graphql`
  fragment TwoColumnImageWithText on ContentfulTwoColumnImageWithText {
    sys {
      contentType {
        sys {
          id
        }
      }
    }
    images {
      gatsbyImageData(aspectRatio: 1.78, width: 736, layout: CONSTRAINED)
    }
    titleText {
      raw
    }
    paragraphText {
      raw
    }
  }
`;

export const getTwoColumnImageWithText: GetComponent = (
  componentId,
  component,
) =>
  isDataType<TwoColumnImageWithTextGQL>(
    componentId,
    component,
    `twoColumnImageWithText`,
  ) ? (
    <TwoColumnImageWithText
      images={
        component.images.map((image) => image.gatsbyImageData) as [
          IGatsbyImageData,
          IGatsbyImageData,
        ]
      }
      paragraphText={component.paragraphText}
      titleText={component.titleText}
    />
  ) : undefined;

export default TwoColumnImageWithText;
