import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React, { ComponentProps } from 'react';
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from 'gatsby-source-contentful/rich-text';
import { renderStyledRichText } from '@/theme/utils';
import {
  AspectRatioWrapper,
  WidthBoundary,
  Max767View,
  Min768View,
} from '@/components';
import styled from '@emotion/styled';
import { colors } from '@/theme';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';

const {
  Boundaries,
  Container,
  ImageContainer,
  TextContainer,
  Image,
  NarrowContainer,
  NarrowImage,
} = {
  Boundaries: styled.div`
    display: flex;
    width: 75%;
    justify-content: center;
  `,
  Container: styled.div`
    padding: 32px;
    display: flex;
    justify-content: center;
  `,
  ImageContainer: styled.div`
    margin-right: 16px;
    display: flex;
    flex: 1;
    justify-content: flex-end;
  `,

  Image: styled(GatsbyImage)`
    max-width: 512px;
    max-height: 512px;
  `,
  TextContainer: styled.div`
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-left: 16px;
  `,
  NarrowContainer: styled.div`
    padding: 32px;
  `,
  NarrowImage: styled(GatsbyImage)`
    margin-bottom: 32px;
  `,
};

type Props = {
  image: ComponentProps<typeof GatsbyImage>['image'];
  imageDescription: string;
  text: RenderRichTextData<ContentfulRichTextGatsbyReference>;
};

const SquareImageWithText: React.FC<Props> = ({
  image,
  text,
  imageDescription,
}) => {
  return (
    <WidthBoundary backgroundStyle={{ backgroundColor: colors.background }}>
      <Min768View>
        <Container>
          <Boundaries>
            <ImageContainer>
              <AspectRatioWrapper aspectRatio={1}>
                <Image alt={imageDescription} image={image} />
              </AspectRatioWrapper>
            </ImageContainer>
            <TextContainer>{renderStyledRichText(text)}</TextContainer>
          </Boundaries>
        </Container>
      </Min768View>
      <Max767View>
        <NarrowContainer>
          <NarrowImage alt={imageDescription} image={image} />
          {renderStyledRichText(text)}
        </NarrowContainer>
      </Max767View>
    </WidthBoundary>
  );
};

type SquareImageWithTextDataGQL = {
  imageDescription: string;
  image: {
    gatsbyImageData: IGatsbyImageData;
  };
  text: RenderRichTextData<ContentfulRichTextGatsbyReference>;
} & ComponentId;

export const query = graphql`
  fragment SquareImageWithText on ContentfulSquareImageWithText {
    imageDescription
    image {
      gatsbyImageData(aspectRatio: 1, width: 736, layout: CONSTRAINED)
    }
    text {
      raw
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getSquareImageWithText: GetComponent = (componentId, component) =>
  isDataType<SquareImageWithTextDataGQL>(
    componentId,
    component,
    `squareImageWithText`,
  ) ? (
    <SquareImageWithText
      imageDescription={component.imageDescription}
      image={component.image.gatsbyImageData}
      text={component.text}
    />
  ) : undefined;

export default SquareImageWithText;
