import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import { ImageList as MUIImageList, ImageListItem } from '@mui/material';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import { WidthBoundary } from '@/components';

type Props = {
  images: { rows: number; cols: number; image: IGatsbyImageData }[];
};

const ImageList: React.FC<Props> = ({ images }) => {
  const imageListRef = useRef(null);
  const [width, setWidth] = useState(null);

  useLayoutEffect(() => {
    console.log('hello2');
    function handleResize() {
      console.log('hello');
      setWidth(imageListRef.current.offsetWidth);
    }
    handleResize();
    window.addEventListener(`resize`, handleResize);
    return window.removeEventListener(`resize`, handleResize);
  });

  return (
    <WidthBoundary>
      <MUIImageList
        ref={imageListRef}
        variant="quilted"
        cols={4}
        rowHeight={width ? width / 4 : undefined}
        gap={8}
      >
        {images.map((imageData, index) => (
          <ImageListItem
            key={`ImageList${index}`}
            cols={imageData.cols}
            rows={imageData.rows}
          >
            <GatsbyImage
              style={{ position: 'initial' }}
              image={imageData.image}
              alt={`Afbeelding ${index} in afbeeldingslijst`}
            />
          </ImageListItem>
        ))}
      </MUIImageList>
    </WidthBoundary>
  );
};

export type ImageListGQL = {
  images: {
    image: { gatsbyImageData: IGatsbyImageData };
    rows: number;
    cols: number;
  }[];
} & ComponentId;

export const query = graphql`
  fragment ImageList on ContentfulImageList {
    images {
      image {
        gatsbyImageData
      }
      rows
      cols
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getImageList: GetComponent = (componentId, component) =>
  isDataType<ImageListGQL>(componentId, component, `imageList`) ? (
    <ImageList
      images={component.images.map((imageData) => ({
        ...imageData,
        image: imageData.image.gatsbyImageData,
      }))}
    />
  ) : undefined;

export default ImageList;
