import React from 'react';
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from 'gatsby-source-contentful/rich-text';
import { renderStyledRichText } from '@/theme/utils';
import { DefaultContainer } from '@/components';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';

type Props = {
  title: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  center?: boolean;
  defaultContainer?: boolean;
};

const Title: React.FC<Props> = ({ title, defaultContainer = true, center }) => {
  const StyledTitle = styled.div`
    text-align: ${center ? `center` : `initial`};
  `;
  return (
    <DefaultContainer enable={defaultContainer}>
      <StyledTitle>{renderStyledRichText(title)}</StyledTitle>
    </DefaultContainer>
  );
};

export type TitleGQL = {
  title: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  center: boolean;
} & ComponentId;

export const query = graphql`
  fragment Title on ContentfulTitle {
    title {
      raw
    }
    center
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getTitles: GetComponent = (componentId, component, args) =>
  isDataType<TitleGQL>(componentId, component, `titles`) ? (
    <Title
      title={component.title}
      defaultContainer={args?.defaultContainer}
      center={component.center}
    />
  ) : undefined;

export default Title;
