import React from 'react';
import styled from '@emotion/styled';
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from 'gatsby-source-contentful/rich-text';
import { renderStyledRichText } from '@/theme/utils';
import { DefaultContainer, Max767View, Min768View } from '@/components';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';

type Props = {
  text: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  twoColumn: boolean;
  style: React.CSSProperties;
  textSize: 'small' | 'medium' | 'large';
  defaultContainer?: boolean;
};

const TextWrapper = styled.div(
  ({ twoColumn, style }: Pick<Props, 'twoColumn' | 'style'>) => ({
    ...style,
    ...(twoColumn
      ? {
          columnCount: 2,
          columnGap: `32px`,
          p: {
            marginTop: 0,
          },
        }
      : {}),
  }),
);

const Text: React.FC<Props> = ({
  text,
  textSize,
  twoColumn,
  style,
  defaultContainer = true,
}) => {
  return (
    <DefaultContainer enable={defaultContainer}>
      <Min768View>
        <TextWrapper style={style} twoColumn={twoColumn}>
          {renderStyledRichText(text, { textSize })}
        </TextWrapper>
      </Min768View>
      <Max767View>{renderStyledRichText(text)}</Max767View>
    </DefaultContainer>
  );
};

export type TextGQL = {
  text: RenderRichTextData<ContentfulRichTextGatsbyReference>;
  textSize: 'small' | 'medium' | 'large';
  style: Object;
  twoColumn: boolean;
} & ComponentId;

export const query = graphql`
  fragment Text on ContentfulText {
    text {
      raw
    }
    style {
      width
      margin
      marginTop
      marginBottom
      textAlign
    }
    twoColumn
    textSize
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getText: GetComponent = (componentId, component, args) =>
  isDataType<TextGQL>(componentId, component, `text`) ? (
    <Text
      text={component.text}
      twoColumn={component.twoColumn}
      textSize={component.textSize}
      style={component.style}
      defaultContainer={args?.defaultContainer}
    />
  ) : undefined;

export default Text;
