import React from 'react';
import { DefaultContainer } from '@/components';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { colors } from '@/theme';

type Props = {
  defaultContainer: boolean;
  headline: string;
};

const ByVtHeadline: React.FC<Props> = ({ headline, defaultContainer }) => {
  const { Container, Prefix, Title } = {
    Container: styled.div`
      display: flex;
      justify-content: center;
      align-items: center;
      flex: row;
    `,
    Prefix: styled.div`
      text-align: center;
      font-size: 40px;
      line-height: 72px;
      color: ${colors.textDefault};
    `,
    Title: styled.h6`
      text-align: center;
      line-height: 72px;
    `,
  };
  return (
    <DefaultContainer enable={defaultContainer}>
      <Container>
        <Prefix>By VT |</Prefix>
        <Title>{headline}</Title>
      </Container>
    </DefaultContainer>
  );
};

export type ByVtHeadlineGQL = {
  headline: string;
} & ComponentId;

export const query = graphql`
  fragment ByVtHeadline on ContentfulByVtHeadline {
    headline
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getByVtHeadline: GetComponent = (componentId, component, args) =>
  isDataType<ByVtHeadlineGQL>(componentId, component, `byVtHeadline`) ? (
    <ByVtHeadline
      headline={component.headline}
      defaultContainer={args?.defaultContainer}
    />
  ) : undefined;

export default ByVtHeadline;
