import { ComponentId, ComponentIds, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import React, { ReactNode } from 'react';
import { renderGQLComponents } from '..';

type Props = {
  children?: ReactNode;
};

const Column = styled.div`
  display: flex;
  flex-direction: column;
`;

const Vertical: React.FC<Props> = ({ children }) => {
  return <Column>{children}</Column>;
};

export type VerticalGQL = {
  items: any;
} & ComponentId;

export const query = graphql`
  fragment Vertical on ContentfulVertical {
    items {
      ...Image
      ...Text
      ...Title
      ...ByVtHeadline
      ...HorizontalWOVertical
      ...Button
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getVertical = (componentId: ComponentIds, component: any) =>
  isDataType<VerticalGQL>(componentId, component, `vertical`) ? (
    <Vertical>
      {renderGQLComponents(component.items, { defaultContainer: false })}
    </Vertical>
  ) : undefined;

export default Vertical;
