import { WidthBoundary } from '@/components';
import { colors } from '@/theme';
import { ComponentId, isListType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';
import React from 'react';
import { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

interface ReviewProps {
  title: string;
  writers: string;
  body: string;
}

const ReviewStyles = {
  Container: styled.div`
    display: flex;

    flex-direction: column;
    text-align: center;
    padding-left: 88px;
    padding-right: 88px;
    padding-bottom: 16px;

    h3 {
      margin-top: 24px;
      margin-bottom: 8px;
    }
  `,
  Title: styled.div`
    h4 {
      text-align: center;
      font-size: 20px;
      font-weight: 300;
    }
  `,
};

const Review: React.FC<ReviewProps> = ({ title, body, writers }) => {
  const { Container } = ReviewStyles;

  return (
    <Container>
      <h3>{title}</h3>
      <p>{body}</p>
      <h4>-{writers}</h4>
    </Container>
  );
};

const StyledSwiper = styled(Swiper)`
  .swiper-button-prev {
    left: 32px;
    color: ${colors.primary};
  }
  .swiper-button-next {
    right: 32px;
    color: ${colors.primary};
  }
  .swiper-button-disabled {
    display: none;
  }
  .swiper-pagination {
    position: relative;
  }
  .swiper-pagination-bullet {
    background-color: ${colors.primary};
  }

  padding-left: 32px;
  padding-right: 32px;
  padding-top: 16px;
  background-color: ${colors.backgroundTint};
`;

interface Props {
  reviews: ReviewProps[];
}

const Reviews: React.FC<Props> = ({ reviews }) => {
  return (
    <WidthBoundary
      backgroundStyle={{
        backgroundColor: colors.backgroundTint,
        paddingBottom: 64,
        paddingTop: 64,
      }}
      maxWidth="1392px"
    >
      <ReviewStyles.Title>
        <h4>Lieve woorden van bruidsparen</h4>
      </ReviewStyles.Title>
      <StyledSwiper
        pagination={{
          clickable: true,
        }}
        navigation={reviews.length > 1}
        modules={[Navigation, Pagination]}
        spaceBetween={64}
        slidesPerView={1}
      >
        {reviews.map((props, index) => (
          <SwiperSlide key={index}>
            <Review {...props} />
          </SwiperSlide>
        ))}
      </StyledSwiper>
    </WidthBoundary>
  );
};

type ReviewGQL = {
  title: string;
  writers: string;
  body: { body: string };
} & ComponentId;

export const query = graphql`
  fragment Review on ContentfulReview {
    writers
    title
    body {
      body
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getReviews = (componentList: any[]) =>
  isListType<ReviewGQL>(componentList, `review`) ? (
    <Reviews
      reviews={componentList.map((review) => ({
        ...review,
        body: review.body.body,
      }))}
    />
  ) : undefined;

export default Reviews;
