import { colors } from '@/theme';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import { graphql } from 'gatsby';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';
import React from 'react';
import { Navigation } from 'swiper';
import { SwiperSlide, Swiper } from 'swiper/react';
import styled from '@emotion/styled';
import { CSSProperties } from '@emotion/serialize';

interface Props {
  images: IGatsbyImageData[];
  height: number;
  myStyle: CSSProperties;
}

const { Container } = {
  Container: styled.div(
    ({ height, myStyle }: { height: number; myStyle: CSSProperties }) => ({
      display: `flex`,
      overflow: `hidden`,
      justifyContent: `center`,
      height,
      backgroundColor: colors.backgroundTint,
      ...myStyle,
    }),
  ),
};

const ImageRow: React.FC<Props> = ({ images, height, myStyle }) => {
  console.log(myStyle);
  const StyledSwiper = styled(Swiper)`
    .swiper-button-prev {
      margin-left: 32px;
      left: 0;
      :after {
        margin-right: 4px;
      }
    }

    .swiper-button-next {
      margin-right: 32px;
      right: 0;
      :after {
        margin-right: -4px;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      border-radius: 50%;
      color: ${colors.primaryDark};
      width: 48px;
      height: 48px;
      background-color: ${colors.background}90;

      :after {
        font-size: 32px;
      }
      :active {
        background-color: ${colors.backgroundTint};
      }
    }

    .swiper-button-disabled {
      display: none;
    }
    .swiper-pagination {
      position: relative;
      padding-bottom: 32px;
    }
    .swiper-pagination-bullet {
      background-color: ${colors.primary};
    }
  `;
  return (
    <Container height={height} myStyle={myStyle}>
      <StyledSwiper
        modules={[Navigation]}
        slidesPerView={`auto`}
        centeredSlides={true}
        navigation={true}
        loop
        spaceBetween={16}
      >
        {images.map((image, index) => (
          <SwiperSlide key={index} style={{ flexShrink: 1 }}>
            <div style={{ width: image.width }}>
              <GatsbyImage
                objectFit="none"
                image={image}
                alt={`Afbeelding ${index} in rij`}
              />
            </div>
          </SwiperSlide>
        ))}
      </StyledSwiper>
    </Container>
  );
};

type ImageRowGQL = {
  images: { gatsbyImageData: IGatsbyImageData }[];
  style: CSSProperties;
} & ComponentId;

export const query = graphql`
  fragment ImageRow on ContentfulImageRow {
    images {
      gatsbyImageData(height: 444, layout: FIXED)
    }
    style {
      paddingTop
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getImageRow: GetComponent = (componentId, component) =>
  isDataType<ImageRowGQL>(componentId, component, `imageRow`) ? (
    <ImageRow
      height={444} // What's this magic number? :o
      images={component.images.map((image) => image.gatsbyImageData)}
      myStyle={component.style}
    />
  ) : undefined;

export default ImageRow;
