import React from 'react';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { Button as MUIButton } from '@mui/material';
import { Actions, usePageContext } from '@/utils/PageContext';
import { colors } from '@/theme';

type Props = {
  label: string;
  action: keyof Actions;
  alignVertical?: 'top' | 'center' | 'bottom';
  alignHorizontal?: 'left' | 'center' | 'right';
  absolute?: boolean;
};

const Button: React.FC<Props> = ({ label, action }) => {
  const { BTN, Container } = {
    BTN: styled(MUIButton)`
      background-color: ${colors.primary};
    `,
    Container: styled.div`
      display: flex;
      flex-shrink: 1;
    `,
  };
  const actions = usePageContext();
  return (
    <Container>
      <BTN variant="contained" size="large" onClick={actions[action]}>
        {label}
      </BTN>
    </Container>
  );
};

export type ButtonGQL = {
  label: string;
  action: keyof Actions;
  alignVertical?: 'top' | 'center' | 'bottom';
  alignHorizontal?: 'left' | 'center' | 'right';
  absolute?: boolean;
} & ComponentId;

export const query = graphql`
  fragment Button on ContentfulButton {
    label
    action
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getButton: GetComponent = (componentId, component) =>
  isDataType<ButtonGQL>(componentId, component, `button`) ? (
    <Button
      label={component.label}
      action={component.action}
      alignHorizontal={component.alignHorizontal}
      alignVertical={component.alignVertical}
      absolute={component.absolute}
    />
  ) : undefined;

export default Button;
