import SquareImageWithText, {
  getSquareImageWithText,
} from './SquareImageWithText';
import ImageRow, { getImageRow } from './ImageRow';
import TwoColumnImageWithText, {
  getTwoColumnImageWithText,
} from './TwoColumnImageWithText';
import Reviews, { getReviews } from './Reviews';
import HeroImage, { getHeroImage } from './HeroImage';
import Text, { getText } from './Text';
import Title, { getTitles } from './Title';
import Vertical, { getVertical } from './Vertical';
import Horizontal, { getHorizontal } from './Horizontal';
import { graphql } from 'gatsby';
import {
  ComponentIds,
  GetComponent,
  getComponentId,
  isDataType,
  ListComponentIds,
  ReferenceListGQL,
} from '@/types/cmsModels';
import { getImage } from './Image';
import { getByVtHeadline } from './ByVTHeadline';
import { getButton } from './Button';
import { getImageList } from './ImageList';

// Generic fragments
export const query = graphql`
  fragment ReferenceList on ContentfulReferenceList {
    list {
      ...Review
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
  fragment Page on ContentfulPage {
    hero {
      image {
        gatsbyImageData
      }
    }
    components {
      ...Title
      ...Text
      ...Image
      ...ImageRow
      ...SquareImageWithText
      ...TwoColumnImageWithText
      ...HeroImage
      ...ReferenceList
      ...Horizontal
      ...ImageList
    }
  }
`;

export const getComponentFromGQL: {
  [key in ComponentIds]: GetComponent;
} = {
  squareImageWithText: getSquareImageWithText,
  imageRow: getImageRow,
  twoColumnImageWithText: getTwoColumnImageWithText,
  heroImage: getHeroImage,
  image: getImage,
  text: getText,
  titles: getTitles,
  horizontal: getHorizontal,
  vertical: getVertical,
  byVtHeadline: getByVtHeadline,
  button: getButton,
  imageList: getImageList,
};

export const getListComponentsFromGQL: {
  [key in ListComponentIds]: (componentList: any[]) => JSX.Element | undefined;
} = {
  review: getReviews,
};

export const renderGQLComponents = (
  components: any,
  args?: { [key: string]: any },
): (JSX.Element | undefined)[] | undefined =>
  Array.isArray(components)
    ? components
        .filter((component) => !!component && Object.keys(component).length > 0)
        .map((component) => {
          const componentId = getComponentId(component) as ComponentIds;

          if (
            isDataType<ReferenceListGQL>(
              componentId,
              component,
              `referenceList`,
            )
          ) {
            const validatedList = component.list.filter(
              (item) => Object.keys(item).length > 0,
            );

            const listComponentId = getComponentId(
              validatedList[0],
            ) as ListComponentIds;

            return getListComponentsFromGQL[listComponentId](validatedList);
          }
          return getComponentFromGQL[componentId](componentId, component, args);
        })
    : undefined;

export {
  ImageRow,
  SquareImageWithText,
  TwoColumnImageWithText,
  Reviews,
  HeroImage,
  Horizontal,
  Text,
  Title,
  Vertical,
};
