import React, { ComponentProps } from 'react';
import { DefaultContainer } from '@/components';
import { graphql } from 'gatsby';
import { ComponentId, GetComponent, isDataType } from '@/types/cmsModels';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image';

type Props = {
  image: ComponentProps<typeof GatsbyImage>['image'];
  description: string;
  defaultContainer?: boolean;
};

const Image: React.FC<Props> = ({
  image,
  description,
  defaultContainer = true,
}) => {
  return (
    <DefaultContainer enable={defaultContainer}>
      <GatsbyImage alt={description} image={image} />
    </DefaultContainer>
  );
};

export type ImageGQL = {
  description: string;
  image: {
    gatsbyImageData: IGatsbyImageData;
  };
} & ComponentId;

export const query = graphql`
  fragment Image on ContentfulImage {
    description
    image {
      gatsbyImageData
    }
    sys {
      contentType {
        sys {
          id
        }
      }
    }
  }
`;

export const getImage: GetComponent = (componentId, component, args) =>
  isDataType<ImageGQL>(componentId, component, `image`) ? (
    <Image
      description={component.description}
      image={component.image.gatsbyImageData}
      defaultContainer={args?.defaultContainer}
    />
  ) : undefined;

export default Image;
